<template>
    <div class="pt-10">
      <ModalTipsTricks ref="modalTipsTricks"/>
  
        <div class="grid grid-flow-row gap-2 pt-1">
          <button @click="$noc('#modalTipsTricks').modal('show')" class="border bg-gray-200 py-1">Add New Tips & Tricks</button>
          <div v-for="(item, index) in tipsTricks" :key="item.id">
            <button @click="openTips(index, $event)" class="flex justify-between items-center bg-white shadow border px-2 py-2 w-full">
              <p class="font-semibold">{{ item.name }}</p>
              <i class="fa-solid fa-angle-right"></i>
            </button>
            <div :data-id="'drop-' + index" class="bg-white shadow border mx-2 hidden text-start p-2">
              <div class="flex justify-end items-start">
                <Button @click="prepareUpdate(item)" color="blue" class="!w-8 !h-8"><i class="fa-solid fa-edit"></i></Button>
                <Button @click="deleteTips(item.id, item.name)" color="red" class="!w-8 !h-8"><i class="fa-solid fa-trash"></i></Button>
              </div>
              <div v-html="item.content"></div>
            </div>
          </div>
        </div>

    </div>
</template>

<script>
import Button from '@/components/noc_ui/Button.vue'
import ModalTipsTricks from '@/components/ModalTipsTricks.vue'

export default {
  components: {
    Button,
    ModalTipsTricks
  },
  data() {
    return {
      tipsTricks: null
    }
  },
  methods: {
    async getTipsTricks() {
      this.$loading.is = true
      const { data, error } = await this.$supabase.from("tips_tricks").select('*')
      this.$loading.is = false
      this.tipsTricks = data
    },
    async deleteTips(id, name) {
      this.$Swal.fire({
        html: `<div><b>Do you want to delete?</b><br><p>${name}</p></div>`,
        showDenyButton: false,
        showCancelButton: true,
        confirmButtonText: 'Yes',
        confirmButtonColor: 'red',
      }).then(async (result) => {
        if (result.isConfirmed) {
          this.$loading.is = true
          const { error } = await this.$supabase.from("tips_tricks").delete().eq("id", id)
          this.$loading.is = false
          if (error) {
            this.$Swal.fire("", "Error", "error")
            return
          }
    
          this.$Swal.fire("", "Deleted successfully", "success")
          this.getTipsTricks()
        }
      })
    },
    prepareUpdate(data) {
      this.$refs.modalTipsTricks.is_edit = true
      this.$refs.modalTipsTricks.title = 'Update Tips & Tricks'

      this.$refs.modalTipsTricks.id = data.id
      this.$refs.modalTipsTricks.name = data.name
      this.$refs.modalTipsTricks.content = data.content

      this.$noc('#modalTipsTricks').modal('show')
    },
    openTips(id, e) {
      const element = e.target.parentElement

      let button = element.querySelector('button')
      let div = element.querySelector('div')
      let icon = element.querySelector('button > i')

      if ( div.classList.contains('hidden') ) {
        div.classList.remove('hidden')
        icon.classList.replace('fa-angle-right', 'fa-angle-down')
      } else {
        div.classList.add('hidden')
        icon.classList.replace('fa-angle-down', 'fa-angle-right')
      }
    }
  },
  created() {
    this.getTipsTricks()
    
  }
}
</script>