<template>
    <Modal id="modalTipsTricks" :title="title">
        <div v-if="is_show" class="w-full">
            <Form @onSubmit="processData">
                <Input label="NAME ..." v-model="name" :value="name" />
                <QuillEditor 
                  v-model:content="content"
                  contentType="html"
                  theme="snow"
                  :options="editorOptions"
                ></QuillEditor>
                  <Button type="submit" class="!mx-0 mt-2 w-full">SAVE</Button>
            </Form>
        </div>
    </Modal>
</template>

<script>
import { QuillEditor } from '@vueup/vue-quill'
import Button from '@/components/noc_ui/Button.vue'
import Form from '@/components/noc_ui/XForm.vue'
import Input from '@/components/noc_ui/Input.vue'
import Modal from '@/components/noc_ui/Modal.vue'

export default {
    components: {
        Modal,
        Button,
        Form,
        Input,
        QuillEditor
    },
    data() {
        return {
            is_edit: false,
            is_show: false,
            title: '',
            id: '',
            name: '',
            content: '',
            copied: false,
            editorOptions: {
                modules: {
                toolbar: [
                    [{ header: [1, 2, false] }],
                    ['bold', 'italic', 'underline'],
                    [{ list: 'ordered' }, { list: 'bullet' }],
                    [{ script: 'sub' }, { script: 'super' }],
                    [{ size: ['small', false, 'large', 'huge'] }],
                    [{ color: [] }, { background: [] }],
                    [{ font: [] }],
                    [{ align: [] }],
                    ['clean']
                ]
                }
            }
        }
    },
    methods: {
        async processData() {
            try {
                const formdata = {
                    name: this.name,
                    content: this.content
                }

                let error = null

                this.$loading.is = true
                if ( this.is_edit ) {
                    const { error: err } = await this.$supabase.from("tips_tricks").update(formdata).eq('id', this.id)
                    error = err
                } else {
                    const { error: err } = await this.$supabase.from("tips_tricks").insert(formdata)
                    error = err
                }
                this.$loading.is = false

                if (error) {
                    this.$Swal.fire("", "Error", "error")
                    return
                }

                this.$Swal.fire("", "Successfully", "success")
                this.$noc('#modalTipsTricks').modal('hide')
                this.$parent.getTipsTricks()
            } catch (error) {
                this.$Swal.fire("", "Error Catch", "error")
            }
        },
    },
    mounted() {
        this.$noc('#modalTipsTricks').listen('ui.modal.show', e => {
            this.$nextTick(() => {
                setTimeout(() => {
                    this.is_show = true
                }, 500);
            })
        })

        this.$noc('#modalTipsTricks').listen('ui.modal.hidden', e => {
            this.$nextTick(() => {
                setTimeout(() => {
                    this.is_show = false
                    this.is_edit = false
                    this.title = 'Add New Tips & Tricks'
                    this.id = ''
                    this.name = ''
                    this.content = ''
                }, 500);
            })
        })
    }
}
</script>